<script>
import { mapState, mapActions } from 'vuex'
import Utils from '@/utils/util'
import viewFIle from '@/components/Modal/ViewFile.vue'
export default {
  name: 'HCBPDetail',
  data () {
    return {
      notifications: false,
      sound: true,
      widgets: false,
      alignments: [
        'start',
        'center',
        'end'
      ]
    }
  },
  components: {
    viewFIle
  },
  inject: {
    theme: {
      default: { isDark: false }
    }
  },
  computed: {
    ...mapState('hcbp', {
      state: (state) => state
    })
  },
  methods: {
    ...mapActions('hcbp', [
      'simpan'
    ]),
    ...mapActions('modalFile', [
      'setFile'
    ]),
    toRupiah (nominal) {
      return Utils.toRupiah(nominal)
    },
    toDateTime (tanggal) {
      return Utils.toDateTime(tanggal)
    },
    checkFile (file) {
      if (file) {
        const ext = file.split('.')
        if (ext[4] === 'pdf') {
          return true
        } else {
          return false
        }
      } else {
        return false
      }
    }
  }
}
