<template>
  <div class="mt-4">
    <!-- <br>
          <v-card> -->
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Pengajuan Pinjaman
      </div>
      <JsonExcel v-if="data.data.length > 0" :data="data.data" :fields="json_fields" class="ml-auto">
        <v-btn class="primary"><v-icon color="white">mdi-download</v-icon> Download</v-btn>
      </JsonExcel>
    </v-card-title>
    <!-- </v-card>
          <br> -->
    <v-card class="ma-2">
      <v-card-title>
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details
          width="100px"></v-text-field>
      </v-card-title>
      <v-data-table class="elevation-1" :headers="headers" :items="data.data" loading-text="Loading... Please wait" :loading="data.loading"
        :search="search">
        <template #[`item.index`]="{ item }">
          {{ data.data.indexOf(item) + 1 }}.
        </template>
        <template #[`item.SubmitDate`]="{ item }">
            {{ toDateTime2(item.SubmitDate)}}
        </template>
        <template #[`item.LoanAmount`]="{ item }">
            {{ item.LoanAmount > 0 ? toRupiah(item.LoanAmount) : '0'}}
        </template>
        <template #[`item.InstallmentAmount`]="{ item }">
            {{ item.InstallmentAmount > 0 ? toRupiah(item.InstallmentAmount) : '0'}}
        </template>
        <template #[`item.Tenor`]="{ item }">
            {{ item.Tenor }} Bulan
        </template>
        <template #[`item.action`]="{ item }">
          <v-btn  icon color="primary" @click="detail(item)">
            <v-icon small>
              mdi-eye
            </v-icon>
          </v-btn>
          <v-btn  icon color="warning" @click="edit(item)">
            <v-icon small>
              mdi-lead-pencil
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
    <Detail/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'
import Utils from '@/utils/util'
import Detail from '@/components/HCBP/Detail.vue'
import JsonExcel from 'vue-json-excel'
export default {
  name: 'HCBPView',
  components: {
    Detail, JsonExcel
  },
  data: () => ({
    search: '',
    headers: [
      {
        text: '#',
        align: 'start',
        sortable: false,
        value: 'index'
      },
      {
        text: 'Tgl Pengajuan',
        align: 'start',
        sortable: false,
        value: 'SubmitDate'
      },
      { text: 'NIK', sortable: false, value: 'NIK' },
      { text: 'Jabatan', sortable: false, value: 'JobTitle' },
      { text: 'Lokasi Kerja', sortable: false, value: 'WorkLocation' },
      { text: 'Pinjaman', sortable: false, value: 'LoanAmount' },
      { text: 'Angsuran', sortable: false, value: 'InstallmentAmount' },
      { text: 'Tenor', sortable: false, value: 'Tenor' },
      { text: 'Action', sortable: false, value: 'action' }
    ],
    json_fields: {
      'Tgl Pengajuan': 'SubmitDate',
      NIK: 'NIK',
      Jabatan: 'JobTitle',
      'Lokasi Kerja': 'WorkLocation',
      Pinjaman: 'LoanAmount',
      Angsuran: 'InstallmentAmount',
      Tenor: 'Tenor'
    }
  }),
  mounted: function () {
    store.dispatch('hcbp/reqData')
  },
  computed: {
    ...mapState('hcbp', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('hcbp', [
      'detail', 'edit'
    ]),
    toRupiah (nominal) {
      return Utils.toRupiah(nominal)
    },
    toDateTime (tanggal) {
      return Utils.toDateTime(tanggal)
    },
    toDateTime2 (tanggal) {
      return Utils.toDateStringMount(tanggal)
    }
  }
}
</script>
